@mixin mobile {
  @media screen and (min-width: 414px) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: 1024px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: 1280px) {
    @content;
  }
}

// solve flexbasis 
//   - 
// example of initiation:
//  :
// @include flexbasis(4,30px);
// where 4= quantity of elements ; 30px space between elements (or gap) 
//  4=     ; 30px =    ( ) 
@mixin flexbasis($amount, $gap) {
  flex-basis: calc((100% - (($amount - 1) * $gap)) / $amount);
}
